/* ===========Variables======================== */
:root{
    --text                  :rgb(255, 255, 255);
    --Borders               :rgb(255, 255, 255);
    --black                 :black;
    --shadow                :rgba(0, 0, 0, 0.346);
    --background            :rgb(236, 238, 255);
    --grey                  :rgb(181, 181, 181);
    --white                 :white;
    --basecolor             :rgb(111, 104, 255);
}

.default {
    --text: rgb(255, 255, 255);
    --Borders: rgb(255, 255, 255);
    --black: black;
    --shadow: rgba(0, 0, 0, 0.346);
    --background: rgb(236, 238, 255);
    --grey: rgb(181, 181, 181);
    --white: white;
    --basecolor: rgb(111, 104, 255);
}

.DarkTheme{
    --text          : rgb(255, 255, 255);
    --Borders       : rgb(0 0 0);
    --black         : rgb(158, 158, 158);
    --shadow        : rgba(0, 0, 0, 0.346);
    --background    : rgb(46 46 46);
    --grey          : rgb(181, 181, 181);
    --white         : black;
    --basecolor     : rgb(2 23 103);
}

.greenery{
    --text: rgb(0, 0, 0);
    --Borders: rgb(255, 255, 255);
    --black: rgba(255, 255, 255, 0.796);
    --shadow: rgba(0, 0, 0, 0.346);
    --background: rgb(39, 39, 39);
    --grey: rgb(181, 181, 181);
    --white: rgb(70, 70, 70);
    --basecolor: rgb(0 153 30);
}

.purewhite{
        --text: rgb(0 0 0);
        --Borders: rgb(255 255 255);
        --black: black;
        --shadow: rgba(0, 0, 0, 0.346);
        --background: rgb(240 240 240);
        --grey: rgb(181, 181, 181);
        --white: white;
        --basecolor: rgb(255 255 255);
    }

    
/* =============================================== */
*{
    padding                 : 0%;
    margin                  : 0%;
    transition              : 1s;
}

body{
    overflow-x              : hidden;
    background-color        : var(--background);
}

button{
    cursor: pointer;
}


.DarkTheme img , .greenery img{
    filter: brightness(60%);
}